.mobile-overlay {
  position: fixed;
  z-index: 90;
  background: rgba(0,0,0,0.5);
  color: white;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  transition: all 0.22s ease;

  p {
    margin-bottom: 0;
  }

  &.active {
    opacity: 1;
    visibility: visible;
  }

  @include minbreak(500px) {
    display: none;
  }

  svg {
    margin-bottom: $spacer;
  }
}