.swiper-container {
  width: 100vw;
  height: 100%;
}

.swiper-slide {
  display: flex;
  background-color: $bg;
  align-items: center;
  justify-content: center;

  h1, h2, h3, h4 {
    color: white;
  }

  &.full-bg {
    background-position: center;
    background-size: cover;
    position: relative;

    > div {
      flex-direction: column;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,0.4);
    }
  }

  > div {
    width: 60vw;
    height: 75vh;
    position: relative;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;

    @include maxbreak($mobileBreak) {
      width: 100vw;
      height: 55vh;
    }

    img {
      object-fit: contain;
      max-height: 100%;
      max-width: 100%;
    }
  }
}

.slide-has-text img {
  opacity: 0.3;
}

.slide-text {
  position: absolute;
  text-align: center;
  max-width: 550px;

  @include maxbreak($mobileBreak) {
    padding: 0 ($spacer * 3)
  }

  p:last-child {
    margin-bottom: 0;
  }
}

.swiper-button-prev,
.swiper-button-next {
  width: 50vw;
  height: 100vh;
  top: 0;
  outline: none;

  @include maxbreak($mobileBreak) {
    display: none;
  }

  &:hover svg {
    opacity: 1;
  }

  svg {
    width: 54px;
    height: 34px;
    position: absolute;
    top: 50%;
    opacity: 0.3;
    transition: opacity 0.33s ease;
  }

  svg > * {
    stroke: black;

    .light & {
      stroke: white;
    }
  }
}

.swiper-button-prev {
  left: 0;

  // &:hover {
  //   cursor: url(../assets/images/prevCursor.svg) 24 12,pointer
  // }

  svg {
    left: 0;
    transform: translate(40px, -50%)
  }
}

.swiper-button-next {
  right: 0;

  // &:hover {
  //   cursor: url(../assets/images/nextCursor.svg) 24 12,pointer
  // }

  svg {
    right: 0;
    transform: translate(-40px, -50%)
  }
}

.swiper-lazy-preloader {
  border-width: 1px;
}