footer {
  bottom: 0;

  @include maxbreak($mobileBreak) {
    border-top: $border;
  }

  > * {
    height: 30px;
    line-height: 30px;
  }
}

.contact-toggle {
  &:before {
    content: 'Contact';
    left: 100%;
    padding-left: $spacer * 2;
    transform: translateX(-10px);

    .contact-active & {
      content: 'Close'
    }
  }
}