.collection,
.caption {
  position: absolute;
  line-height: 30px;
  padding: ($spacer * 4) ($spacer * 5);
  z-index: 20;
  color: black;
  opacity: 0.4;
  text-align: center;
  margin: 0;
  
  @include maxbreak($mobileBreak) {
    padding: ($spacer * 2) ($spacer * 3);
    left: 0;
    right: 0;
  }

  .light & {
    opacity: 0;
  }

  .about-active &,
  .thumbs-active & {
    opacity: 0;
  }
}

.collection {
  top: 0;
  left: 117px;

  @include maxbreak($mobileBreak) {
    padding: ($spacer * 2) ($spacer * 3);
    top: 63px;
    left: 0;
  }
}

.caption {
  bottom: 0;
  left: 60px;
  right: 60px;
  margin: 0;

  @include maxbreak($mobileBreak) {
    bottom: 63px;
    left: 0;
    right: 0;
  }
}