.thumbs-container {
  background-color: $bg;
  color: black;
  height: 100vh;
  transform: translateY(100%);
  visibility: hidden;
  transition: all 0.33s ease-in-out;

  .thumbs-active & {
    visibility: visible;
    transform: translateY(0);
  }
}

.thumbs-inner {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 90vh;
  padding: 34px 0 0 ($spacer * 5);
  overflow-y: scroll;

  @include maxbreak($mobileBreak) {
    padding: 87px 0 0 ($spacer * 2);
  }
}

.thumb-group {
  margin-bottom: ($spacer * 3);

  @include maxbreak($mobileBreak) {
    margin-bottom: ($spacer * 3);
  }

  &:last-child {
    min-height: calc(100vh - 58px);

    @include maxbreak($mobileBreak) {
      margin-bottom: 87px;
      min-height: calc(100vh - 173px);
    }

    .thumb-group-items {
      padding-bottom: ($spacer * 8)
    }
  }
}

.thumb-group-heading {
  margin-bottom: ($spacer * 5);
  margin-right: ($spacer * 5);
  padding-left: 117px;

  @include maxbreak($mobileBreak) {
    margin-bottom: ($spacer * 3);
    margin-right: ($spacer * 2);
    padding-left: 0;
  }
}

.thumb-group-items {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  > div {
    width: calc(20% - 40px);
    margin-right: 40px;
    margin-bottom: 40px;
    position: relative;
    overflow: hidden;

    @include maxbreak($mobileBreak) {
      width: calc(33.33333% - 16px);
      margin-right: ($spacer * 2);
      margin-bottom: ($spacer * 2);
    }
  }
}

.thumb-group-item-image {
  height: 0;
  overflow: hidden;
  padding-bottom: 100%;
  background-size: cover;
}

// Toggle

.thumbs-toggle,
.contact-toggle {
  position: relative;

  &:before {
    position: absolute;
    line-height: 30px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.22s ease;
  }

  &:hover:before {
    opacity: 0.4;
    visibility: visible;
    transform: translateX(0);
  }
}

.thumbs-toggle {
  &:before {
    content: 'Thumbs';
    padding-right: $spacer * 2;
    transform: translateX(10px);
    right: 100%;

    .thumbs-active & {
      content: 'Close'
    }
  }
}