header,footer {
  position: absolute;
  left: 0;
  right: 0;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: black;
  padding: ($spacer * 4) ($spacer * 5);

  @include maxbreak($mobileBreak) {
    padding: ($spacer * 2) ($spacer * 3);
  }

  svg {
    width: 30px;
    height: 30px;

    @include maxbreak($mobileBreak) {
      width: 24px;
      height: 24px;
    }
  }

  svg > * {
    stroke: black;
  }

  .light &, .about-active &, .contact-active & {
    color: white;

    svg > * {
      stroke: white;
    }
  }

  .light.thumbs-active & {
    color: black;

    svg > * {
      stroke: black;
    }
  }

  .thumbs-active & {
    @include maxbreak($mobileBreak) {
      background-color: rgba($bg, 0.98);
    }
  }
}

header {
  top: 0;
  padding-bottom: ($spacer * 3);
  pointer-events: none;

  @include maxbreak($mobileBreak) {
    border-bottom: $border;
    padding-bottom: ($spacer * 2);
  }  

  > div {
    pointer-events: all;
  }

  > div:first-child a {
    display: inline-block;
    line-height: 30px;
  }

  > div:last-child {
    .about-toggle {
      .about-active &,
      .thumbs-active &,
      .contact-active & {
        display: none;
      }
    }
  }

  a,
  .link {
    transition: opacity 0.22s ease;
    
    @include minbreak($mobileBreak) {
      &:hover {
        opacity: 0.4;
      }
    }
  }

  .overlay-toggle {
    display: none;
    height: 30px;
    transition: opacity 0.22s ease;

    svg {
      width: 28px;
      height: 28px;

      @include maxbreak($mobileBreak) {
        height: 22px;
        width: 22px;
      }
    }

    @include minbreak($mobileBreak) {
      &:hover {
        opacity: 0.4;
      }
    }

    @include maxbreak($mobileBreak) {
      height: 24px;
    }

    .about-active &,
    .thumbs-active &,
    .contact-active & {
      display: block;
    }
  }
}