.about-toggle {
  span span {
    display: inline-block;
    padding: 0 ($spacer * 2);
    opacity: 0.4;
  }
}

.thumbs-container,
.about-container,
.contact-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  display: flex;
  z-index: 40;
}

.about-container,
.contact-container {
  height: 100%;
  background: black;
  color: white;
  align-items: center;
  justify-content: center;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.33s ease;
}

.about-active .about-container {
  opacity: 1;
  visibility: visible;
}

.contact-active .contact-container {
  opacity: 1;
  visibility: visible;
}

.about-inner,
.contact-inner {
  width: 50vw;
  max-width: 560px;
  display: flex;

  @include maxbreak($mobileBreak) {
    width: 100vw;
    flex-direction: column;
    padding: 0 ($spacer * 3)
  }

  > div:first-child {
    margin-right: ($spacer * 9);

    @include maxbreak($mobileBreak) {
      margin-right: 0;
      margin-bottom: ($spacer * 2);
    }
  }
}