//$bodyFont: "Heebo",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
$bodyFont: "Muli",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
//$headingFont: "Fjalla One",-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen-Sans,Ubuntu,Cantarell,"Helvetica Neue",sans-serif;
$spacer: 8px;
$mobileBreak: 500px;
$border: 1px solid rgba(black, 0.1);
//$bg: #FAF8F5;
$bg: #ffffff;

@mixin maxbreak($point) {
  @media all and (max-width: $point) { 
    @content; 
  }
}

@mixin minbreak($point) {
  @media all and (min-width: $point) { 
    @content; 
  }
}

html,
body,
#root,
.page_wrap
 {
  height: 100%;
}

body {
  font-family: $bodyFont;
  background-color: $bg;
  font-size: 17px;
  overflow: hidden;
  -webkit-font-smoothing: antialiased;

  @include maxbreak($mobileBreak) {
    font-size: 15px;
  }
}

h1, h2, h3, h4 {
  font-weight: 400;
  color: inherit;
}

h1 {
  font-size: 180px;

  @include maxbreak(1340px) {
    font-size: 140px;
  }

  @include maxbreak(1140px) {
    font-size: 90px;
  }

  @include maxbreak(840px) {
    font-size: 65px;
  }

  @include maxbreak($mobileBreak) {
    font-size: 65px;
  }
}

p {
  margin-bottom: $spacer * 4;
  line-height: 1.7em;
}

a,
.link,
.slide-link {
  text-decoration: none;
  color: inherit;

  @include minbreak($mobileBreak) {
    &:hover {
      cursor: pointer;
    }
  }
}

.underline {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    height: 1px;
    background: white;
  }
}

.muted {
  color: inherit;
  opacity: 0.4;
}